.forms-wrapper {
  width: 400px;
  height: 400px;
  margin: 80px auto;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;

  @media (max-width: 1070px) {
    width: 360px;
    height: 360px;
  }

  @media (max-width: 390px) {
    width: 340px;
  }
}
