.form-button {
  width: 180px;
  height: 34px;
  font-family: "Roboto", sans-serif;
  background-color: var(--main-color);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  box-shadow: 0 1px 2px 0px black;
  cursor: pointer;
  opacity: 1;
  transition: all 0.3s ease;

  @media (max-width: 1070px) {
    width: 170px;
    height: 32px;
  }

  @media (max-width: 600px) {
    cursor: default;
  }

  &:active {
    transform: translateY(-5px);
    opacity: 0.5;
    box-shadow: none;
  }

  &-text {
    font-size: 15px;
    color: #fff;

    @media (max-width: 1070px) {
      font-size: 14px;
    }
  }
}
